import React, { Component } from 'react';
import { Row, Col, Form, Button, Dropdown } from 'react-bootstrap';
import _ from 'lodash'

class Password extends Component{

    constructor(props)
    {
        super(props);

        this.state={
            user:null
        }
    }


    static getDerivedStateFromProps(props, state) {
        return {
            user: props.user,
            Pswdmsg: props.Pswdmsg,
            variant: props.variant,
        }
    }

    handleSubmit = () => {
        let {currentPassword, newPassword, confirmPassword} = this.pswdForm;
        this.props.submitPassword(currentPassword.value,newPassword.value,confirmPassword.value);
    }

    clearPswdField = () => {
        // this.pswdForm.reset();
    }

    render()
    {

        if(this.state.variant === 'green')
        {
            this.clearPswdField();
        }

        return (
            <div id="password">
                <div className="head">Change Password</div>
                <Row className="m-0">
                    <Col md={7}>
                        <Form
                            ref={pswdForm => (this.pswdForm = pswdForm)}>
                            <Form.Group as={Row} controlId="current-password" className="mb-4">
                                <Form.Label column sm={3} className="pr-0">
                                    Current Password*:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="password" name="currentPassword" placeholder="Current Password" />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="new-password" className="mb-4">
                                <Form.Label column sm={3} className="pr-0">
                                    New Password*:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="password" name="newPassword" placeholder="New Password" />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="confirm-password" className="mb-4">
                                <Form.Label column sm={3} className="pr-0">
                                    Confirm Password*:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="password" name="confirmPassword" className="mb-4" placeholder="Confirm Password" />
                                    <p style={{color:`${this.state.variant}`}} >{this.state.Pswdmsg}</p>
                                    <Button className="btn-submit" onClick={this.handleSubmit}>Save</Button>
                                    <Button className="btn-submit btn-cancel" onClick={this.clearPswdField}>Cancel</Button>
                                </Col>
                            </Form.Group>
                        </Form>
                    </Col>
                    <Col md={5}></Col>
                </Row>
            </div>
        )
    }
}

export default Password;
